  height: 600px;
}
.index .carousel img {
  object-fit: cover;
  margin: auto;
  display: block;
  height: 600px;
}
.index .carousel li button {
  background-color: rgba(0, 0, 0, 0.7) !important;
}
.index .carousel .slick-active button {
  background: #f30 !important;
}
.index .wrap {
  width: 1200px;
  margin: 0 auto;
}
.index .module > .title {
  font-size: 24px;
  color: #000;
  line-height: 70px;
  margin-top: 50px;
}
.index .module .items {
  overflow: hidden;
}
.index .module button {
  width: 80px;
  padding: 8px;
  float: right;
  position: relative;
  top: 30px;
  right: 60px;
}
