___CSS_LOADER_EXPORT___.push([module.id, `.page.item {
  background: #fff;
  min-height: 100vh;
}
.page.item .main {
  width: 1200px;
  margin: 0 auto ;
  position: relative;
  padding-left: 600px;
  box-sizing: border-box;
  padding-bottom: 30px;
}
.page.item .main .image-display {
  width: 560px;
  height: 660px;
  position: absolute;
  left: 0;
  top: 0;
}
.page.item .main .image-display .pic {
  width: 560px;
  height: 560px;
}
.page.item .main .image-display .pic img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}
.page.item .main .image-display .thumb {
  margin-top: 20px;
}
.page.item .main .image-display .thumb div.img {
  box-sizing: border-box;
  width: 80px;
  height: 80px;
  border: solid 1px #ddd;
  display: inline-block;
  margin: 0 5px;
  cursor: pointer;
}
.page.item .main .image-display .thumb div.img.active {
  border-color: #f30;
}
.page.item .main .image-display .thumb div.img img {
  display: block;
  object-fit: cover;
  max-width: 100%;
  max-height: 100%;
}
.page.item .main .info {
  padding-top: 39px ;
}
.page.item .main .info .title {
  color: #333;
  font-size: 28px;
  line-height: 40px;
}
.page.item .main .info .sub-title {
  color: #666;
  font-size: 16px;
  margin-top: 20px;
}
.page.item .main .info .pric-box {
  margin-top: 30px;
  height: 80px;
  background-image: linear-gradient(1.5708rad, #ff0d40, #ff91a8);
  border-radius: 10px;
  padding-left: 20px;
  padding-top: 10px ;
}
.page.item .main .info .pric-box .tip {
  background: rgba(255, 255, 255, 0.5);
  color: #fff;
  display: inline-block;
  font-size: 14px;
  padding: 3px 10px;
}
.page.item .main .info .pric-box .price,
.page.item .main .info .pric-box .origin-price {
  display: inline-block;
  margin-right: 10px;
  color: #fff;
}
.page.item .main .info .pric-box .price {
  font-size: 30px;
}
.page.item .main .info .pric-box .price span {
  font-size: 20px;
}
.page.item .main .info .pric-box .origin-price {
  font-size: 16px;
  text-decoration: line-through;
}
.page.item .main .info .pric-box .origin-price span {
  font-size: 20px ;
  margin-top: 30px;
}
.page.item .main .info .sku-box .title {
  font-size: 16px;
}
.page.item .main .info .sku-box .sku-list {
  overflow: hidden;
}
.page.item .main .info .sku-box .sku-list .sku {
  width: 260px;
  height: 56px;
  line-height: 56px ;
  border: solid 1px #ddd;
  text-align: center;
  display: inline-block;
  border-radius: 10px;
  margin-right: 20px;
  margin-top: 15px;
  font-size: 14px;
  cursor: pointer;
}
.page.item .main .info .sku-box .sku-list .sku.active {
  color: #f30;
  border-color: #f30;
}
.page.item .main .info .op {
  margin-top: 50px;
}
.page.item .main .info .op div {
  display: inline-block;
  width: 220px;
  text-align: center;
  font-size: 22px;
  height: 50px;
  line-height: 52px;
  color: #fff;
  border-radius: 56px;
  margin-left: 35px;
}
.page.item .main .info .op .add-cart {
  background: linear-gradient(1.5708rad, #ffa848, #ff904d);
}
.page.item .main .info .op .buy {
  background: linear-gradient(1.5708rad, #fe5945, #d61a19);
}
.page.item .detail {
  border-top: solid 10px #eee;
}
.page.item .detail img {
  display: block;
  width: 100%;
}
